@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@900&display=swap');

html, body {
    font-family: "Poppins";
    overflow-x: hidden;
    background-color: #fff;
    margin: 0;
}

input {
    width: 100%;
    font-size: 1.2rem;
    display: inline-block;
    box-sizing: border-box;
    padding: 7px;
    border: 1px solid #cccccc;
    color: #3d3d3d;
}

select {
    width: 100%;
    background-color: #fff;
    font-size: 1.2rem;
    display: inline-block;
    box-sizing: border-box;
    padding: 7px;
    border: 1px solid #cccccc;
    color: #3d3d3d;
}

.text-center{
    text-align: center;
}

.text-right{
    align-items: right;
}

.icon{
    max-width: 17px;
    margin-right: 8px;
}

a{
    text-decoration: none;
}

.btn{
    border-radius: 6px;
    cursor: pointer;
}

.btn-cart{
    background-color: #fff;
    border: 3px solid #E9E9E9;
    border-radius: 50px;
    width: 60%;
    padding: 10px;
    font-size: 1rem;
}

.btn-cart:hover{
    background-color: #ececec;
}

.btn-red{
    background-color: #e84f3d;
    color: #fff;
    border: 0;
    padding: 10px;
    font-size: 1rem;
}

.btn-checkout {
    width: 100%;
    padding: 20px;
    height: 70px;
    font-size: 1.5rem;
    font-weight: bold;
    border: 0px;
    border-radius: 0;
    background-color: #e84f3d;
    color: #fff;
    cursor: pointer;
}

.btn-checkout:hover {
    background-color: #b63527;
}

.container{
    margin-left: 30px;
    margin-right: 30px;
    max-width: 90%;
    margin: auto;

}

.titulo h1{
    font-size: 2.5rem;
}

.subtitulo{
    color: #747474;
    font-size: 1.2rem;
    max-width: 50%;
    margin: auto;
}

.col-3 {
    width: 33%;
    display: inline-block;
    vertical-align: top;
}

.input-group {
    margin-bottom: 30px;
}

table {
    width: 100%;
}

td {
    border-bottom: 1px solid #cccccc;
    padding: 15px;
}

.text-red {
    color: #cd3e2e;
}

.text-light {
    color: #747474;
}

@media screen and (max-width: 1100px) {
    .subtitulo{
        max-width: 95%;
    }

    .col-3 {
        width: 100%;
    }
}

